"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { ScrollArea } from "@/components/ui/scroll-area";
import { toast } from "sonner";

interface ResumeAnalysis {
  skills: string[];
  experience: string;
  strengths: string[];
  suggestedTopics: string[];
  summary: string;
}

interface ResumeCardProps {
  resume: {
    id: string;
    fileName: string;
    content: string;
    analysis: ResumeAnalysis | null;
    uploadedAt: Date;
  };
}

export function ResumeCard({ resume }: ResumeCardProps) {
  const router = useRouter();
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [isDeleting, setIsDeleting] = useState(false);

  const handleAnalyze = async () => {
    setIsAnalyzing(true);
    try {
      const response = await fetch(`/api/resume/${resume.id}/analyze`, {
        method: "POST",
      });

      if (!response.ok) {
        throw new Error("Failed to analyze resume");
      }

      toast.success("Resume analyzed successfully!");
      router.refresh();
    } catch {
      toast.error("Failed to analyze resume. Make sure your OpenAI API key is configured.");
    } finally {
      setIsAnalyzing(false);
    }
  };

  const handleDelete = async () => {
    setIsDeleting(true);
    try {
      const response = await fetch(`/api/resume/${resume.id}`, {
        method: "DELETE",
      });

      if (!response.ok) {
        throw new Error("Failed to delete resume");
      }

      toast.success("Resume deleted");
      router.refresh();
    } catch {
      toast.error("Failed to delete resume");
    } finally {
      setIsDeleting(false);
    }
  };

  return (
    <Card>
      <CardHeader className="flex flex-row items-start justify-between">
        <div>
          <CardTitle className="text-lg">{resume.fileName}</CardTitle>
          <CardDescription>
            Uploaded on {new Date(resume.uploadedAt).toLocaleDateString()}
          </CardDescription>
        </div>
        <div className="flex gap-2">
          <Dialog>
            <DialogTrigger asChild>
              <Button variant="outline" size="sm">
                View
              </Button>
            </DialogTrigger>
            <DialogContent className="max-w-2xl max-h-[80vh]">
              <DialogHeader>
                <DialogTitle>{resume.fileName}</DialogTitle>
                <DialogDescription>Resume content</DialogDescription>
              </DialogHeader>
              <ScrollArea className="h-[60vh]">
                <pre className="whitespace-pre-wrap text-sm p-4 bg-muted rounded-lg">
                  {resume.content}
                </pre>
              </ScrollArea>
            </DialogContent>
          </Dialog>

          {!resume.analysis && (
            <Button
              size="sm"
              onClick={handleAnalyze}
              disabled={isAnalyzing}
            >
              {isAnalyzing ? "Analyzing..." : "Analyze with AI"}
            </Button>
          )}

          <Button
            variant="destructive"
            size="sm"
            onClick={handleDelete}
            disabled={isDeleting}
          >
            Delete
          </Button>
        </div>
      </CardHeader>

      {resume.analysis && (
        <CardContent className="space-y-4">
          <div>
            <h4 className="font-medium mb-2">Summary</h4>
            <p className="text-sm text-muted-foreground">
              {resume.analysis.summary}
            </p>
          </div>

          <div>
            <h4 className="font-medium mb-2">Skills Identified</h4>
            <div className="flex flex-wrap gap-2">
              {resume.analysis.skills.map((skill) => (
                <Badge key={skill} variant="secondary">
                  {skill}
                </Badge>
              ))}
            </div>
          </div>

          <div>
            <h4 className="font-medium mb-2">Strengths</h4>
            <ul className="text-sm text-muted-foreground list-disc list-inside">
              {resume.analysis.strengths.map((strength, i) => (
                <li key={i}>{strength}</li>
              ))}
            </ul>
          </div>

          <div>
            <h4 className="font-medium mb-2">Suggested Interview Topics</h4>
            <div className="flex flex-wrap gap-2">
              {resume.analysis.suggestedTopics.map((topic) => (
                <Badge key={topic} variant="outline">
                  {topic}
                </Badge>
              ))}
            </div>
          </div>
        </CardContent>
      )}
    </Card>
  );
}
